
import React from "react";
import { Carousel } from 'react-bootstrap';


import  './CarrouselCss.css'
import copo from '../img/copo.webp'
import bacia from '../img/bacia.jpg'
import casa from '../img/casa.jpg'
import cafe from '../img/cafe.jpg'
import cafe2 from '../img/cafe2.jpg'


function Carrousel(){

  return(

      <div className='carrousel' id="home">

              <Carousel>
          <Carousel.Item>
            <img
              className="imagem"
              src={copo}
              alt="First slide"/>
            <Carousel.Caption className="fundo">
              <h3>Bolo de Copo</h3>
              <p>Fofo e Cremoso</p>
            </Carousel.Caption>
          </Carousel.Item>
          
          <Carousel.Item>
            <img
              className="imagem"
              src={bacia}
              alt="Second slide"/>
            <Carousel.Caption className="fundo">
              <h3>Bolo de Bacia</h3>
              <p>Culinria Tpica Nordestina </p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="imagem"
              src={casa}
              alt="Third slide"/>
              <Carousel.Caption className="fundo">
              <h3>Bolo Aniversrio Casamento</h3>
              <p>Comemore seu Casamento de um jeito Doce</p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="imagem"
              src={cafe}
              alt="Third slide"/>
              <Carousel.Caption className="fundo">
              <h3>Cafe Gourmet</h3>
              <p>O sabor do Cafe com aquele Diferencial</p>
            </Carousel.Caption>
          </Carousel.Item>

          <Carousel.Item>
            <img
              className="imagem"
              src={cafe2}
              alt="Third slide"/>
              <Carousel.Caption className="fundo">
              <h3>Cafe Tradicional</h3>
              <p>Expresso</p>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      </div>

  )
}
export default Carrousel;