import  './HeaderCss.css'

import logo from '../img/logovector.png'

const menuItens=document.querySelectorAll('.menu a[href^="#"]')

function getDistanceTop(element){
    const id=element.getAttribute("href");
    return document.querySelector(id).offsetTop;
}

function nativeScroll(distanceTop){
    window.scroll({
        top:distanceTop,
        behavior:'smooth',
    })
}

function scrollToSection(event){
    event.preventDefault();
    const distanceTop = getDistanceTop(event.target)-10;
    nativeScroll(distanceTop);
}
menuItens.forEach((link)=>{
    link.addEventListener("click", scrollToSection);
})

function Header (){
    return(
        <div className='header'>
            <img className='logo' src={logo}/>
            <ul className='head'>
                <li><a href="#home">Home</a></li>
                <li><a href="#Galery">Galeria</a></li>
                <li><a href="#Contato">Contato</a></li>
                <li><a href="#Sobre">Sobre</a></li>
            </ul>
         </div>
    )
}
export default Header;