import{Form,Row,Col} from 'react-bootstrap'

import './ContatoCss.css'
function Contato(){
    return(
        <div className='contact' id='Contato'>
            <div className='glass'>
            <div className='sectionTitleCt'><h1>Contato</h1></div>
            <div className='titleCt'><h2>Queremos te ouvir! Nos ajude seu feedback.
                <br/> Faa pedidos e oramentos</h2></div>
            <Form>
                <Row className="mb-3">
                    <Form.Group as={Col} controlId="formGridName">
                    <span>Nome</span>
                    <Form.Control type="name" placeholder="Nome Completo" />
                    </Form.Group>

                    <Form.Group as={Col} controlId="formGridEmail">
                    <span>Email</span>
                    <Form.Control type="email" placeholder="Seu email" />
                    </Form.Group>
                </Row>

                <Row>
                <Form.Group as={Col} controlId="formGridAddress1">
                    <span>Endereo</span>
                    <Form.Control placeholder="Rua e Numero" />
                </Form.Group>

                <Form.Group as={Col} controlId="formGridAddress2">
                    <span>Bairro</span>
                    <Form.Control placeholder="Seu Bairro" />
                </Form.Group>
                </Row>    

                <Row className="mb-3">
                    <Form.Group as={Col} controlId="formGridCity">
                    <span>Cidade</span>
                    <Form.Control placeholder="Sua Cidade"/>
                    </Form.Group>

                    <Form.Group as={Col} controlId="formGridState">
                    <span>Estado</span>
                    <Form.Control placeholder="Seu Estado" />
                    </Form.Group>
                </Row>
                <button className='btcontato'>
                    Submit
                </button>
            </Form>
            </div>
        </div>
)
}
export default Contato