import {Card} from 'react-bootstrap';
import './GaleryCss.css'

import{useRef,useEffect,useState} from "react";
import{motion} from 'framer-motion';

import copo from '../img/copo.webp'
import bacia from '../img/bacia.jpg'
import casa from '../img/casa.jpg'
import cafe from '../img/cafe.jpg'
import cafe2 from '../img/cafe2.jpg'
import cafe3 from '../img/cafe3.jpg'
import cafe4 from '../img/cafe4.jpg'
import cafe5 from '../img/cafe5.jpg'
import cafe6 from '../img/cafe6.jpg'
import cafe7 from '../img/cafe7.jpg'
import cafe8 from '../img/cafe8.jpg'
import cafe9 from '../img/cafe9.jpg'
import cafe10 from '../img/cafe10.jpg'
import bolo1 from '../img/bolo1.jpg'
import bolo2 from '../img/bolo2.jpg'
import bolo3 from '../img/bolo3.jpg'
import bolo4 from '../img/bolo4.jpg'
import bolo5 from '../img/bolo5.jpg'
import bolo6 from '../img/bolo6.jpg'
import bolo7 from '../img/bolo7.jpg'
import bolo8 from '../img/bolo8.jpg'
import bolo9 from '../img/bolo9.jpg'
import bolo10 from '../img/bolo10.jpg'
import sal1 from '../img/sal1.jpg'
import sal2 from '../img/sal2.jpg'
import sal3 from '../img/sal3.jpg'
import sal4 from '../img/sal4.jpg'
import sal5 from '../img/sal5.jpg'
import sal6 from '../img/sal6.jpg'
import sal7 from '../img/sal7.jpg'
import sal8 from '../img/sal8.jpg'
import sal9 from '../img/sal9.jpg'
import sal10 from '../img/sal10.jpg'

function Galery(){

    const[width,setWidth]=useState(0);
    const carousel=useRef();

    useEffect(()=>{
        setWidth(carousel.current.scrollWidth - carousel.current.offsetWidth);
    },[]);

    
    return(
         
    

    <div className='slideCard' id='Galery'>
        <div className='sectionTitleCard'><h1>Galeria</h1></div>
        <div className='titleCard'><h2>Cafe Gourmet</h2></div>
        <motion.div ref={carousel} 
        drag="x" 
        dragConstraints={{right:0, left:-width}} 
        className='slideCardCoffe'>

            <Card className='Card'>
                <Card.Img variant="top" src={cafe} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={cafe2} />
            </Card>

            <Card className='Card'>
                <Card.Img variant="top" src={cafe3} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={cafe4} />
            </Card>
            <Card className='Card'>
                <Card.Img variant="top" src={cafe5} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={cafe6} />
            </Card>
            <Card className='Card'>
                <Card.Img variant="top" src={cafe7} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={cafe8} />
            </Card>
            <Card className='Card'>
                <Card.Img variant="top" src={cafe9} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={cafe10} />
            </Card>
        </motion.div>

        <div className='titleCard'><h2>Bolos e Tortas</h2></div>
        <motion.div ref={carousel} 
        drag="x" 
        dragConstraints={{right:0, left:-width}} 
        className='slideCardCoffe'>

            <Card className='Card'>
                <Card.Img variant="top" src={bolo1} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={bolo2} />
            </Card>

            <Card className='Card'>
                <Card.Img variant="top" src={bolo3} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={bolo4} />
            </Card>
            <Card className='Card'>
                <Card.Img variant="top" src={bolo5} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={bolo6} />
            </Card>
            <Card className='Card'>
                <Card.Img variant="top" src={bolo7} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={bolo8} />
            </Card>
            <Card className='Card'>
                <Card.Img variant="top" src={bolo9} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={bolo10} />
            </Card>
        </motion.div>



        <div className='titleCard'><h2>Salgados</h2></div>
        <motion.div ref={carousel} 
        drag="x" 
        dragConstraints={{right:0, left: -width}} 
        className='slideCardCoffe'>

            <Card className='Card'>
                <Card.Img variant="top" src={sal1} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={sal2} />
            </Card>

            <Card className='Card'>
                <Card.Img variant="top" src={sal3} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={sal4} />
            </Card>
            <Card className='Card'>
                <Card.Img variant="top" src={sal5} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={sal6} />
            </Card>
            <Card className='Card'>
                <Card.Img variant="top" src={sal7} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={sal8} />
            </Card>
            <Card className='Card'>
                <Card.Img variant="top" src={sal9} />
            </Card>
            
            <Card className='Card'>
                <Card.Img variant="top" src={sal10} />
            </Card>
        </motion.div>


    </div>
)
} 
export default Galery